import React, { useContext } from 'react'
import { AudioSettings } from './Context'
import { FacebookShareButton,
        FacebookIcon,
        TwitterShareButton,
        TwitterIcon } from 'react-share'

//styles
import './css/socials.css'

function Social() {

    const [ settings ] = useContext(AudioSettings)

    return (
        <div className="social-container" style = {{animation: settings.isPlaying ? 'shadows .2s infinite alternate': ''}}>
            <div className="social-icons">
                <FacebookShareButton url = {"http://nogsu.surge.sh/"} quote = {"Karl Sunogan, I love you! Thanks for sharing your favorite songs."} hashtag = "#ILoveYouSoMuchKarlSunogan">
                    <FacebookIcon round={true} size={30} bgStyle={{fill: '#f4f5f6'}} iconFillColor={"#242428"}/>
                </FacebookShareButton>
            </div>
            <p>Warning! It may trigger epilepsy for some users.</p>
        </div>
    )
}

export default Social
