import React, { createContext, useState } from "react";

export const AudioSettings = createContext();

export default function MediaPlayer({children}){

    const [ settings, setSettings ] = useState({
        isPlaying: false,
        darkTheme: localStorage.getItem("darkmode") 
         ? true : false,
    })

    return (
        <AudioSettings.Provider value = {[ settings, setSettings ]}>
            {children}
        </AudioSettings.Provider>
    )
}