import React, { useContext, useEffect, useRef, useState } from 'react'
//context
import MediaPlayer, { AudioSettings } from './Context'
//components
import { songs} from './songs'
import CurrentSongTitle from './CurrentSongTitle'
import Controls from './Controls'
import Duration from './Duration'
import Social from './Social'
//styles
import './css/music.css'
import Vinyl from './Vinyl'

export default function MusicPlayer (){
    return (
        <MediaPlayer>
            <MusicApp/>
        </MediaPlayer>
    )
}

function MusicApp() {
    const [ settings, setSettings ] = useContext(AudioSettings)
    const [ currentSong, setCurrentSong ] = useState(localStorage.getItem('current') ? localStorage.getItem('current') : 0)
    const [ oldSong, setOldSong ] = useState(0)
    let audio = useRef(new Audio(songs[currentSong].song))
    audio.current.setAttribute('preload', 'metadata')


    const shuffleSongs = () => {
        for(let i= songs.length - 1; i > 0; i--){
            const j = Math.floor(Math.random() * (i + 1));
            [songs[i], songs[j]] = [songs[j], songs[i]];
        }
    }

    useEffect(() => {
        
        if(settings.isPlaying){
            play()
        } else {
            pause()
        }
            
    }, [settings])


    useEffect(() => {
        
        localStorage.setItem('current', currentSong)
        if(oldSong !== currentSong){

            if(currentSong > songs.length){
                setCurrentSong(0)
            }

            //load new song if old song was replaced
            audio.current.pause()
            audio.current.src = '' + songs[currentSong].song;
            audio.current.load()
            setOldSong(currentSong)
            document.title = songs[currentSong].title
        }

    }, [currentSong])

    const play = () => {
        return setTimeout(() => {
            audio.current.play()
        }, 400)
    }

    const pause = () => {
        return setTimeout(() => {
            audio.current.pause()
        }, 400)
    }

    const shufflePlayer = () => {
        shuffleSongs()
        nextSong()
    }

    const download = () => {
        window.open(songs[currentSong].song, "_blank")
    }

    const shareOnTwitter = () => {
        let tweet = "I love you Karl Sunogan! Check his favorite songs here: https://nogsu.surge.sh/ Enjoy!";
        window.open(`https://twitter.com/intent/tweet?text=${encodeURIComponent(tweet)}`, "_blank")
    }

    const nextSong = () => {
        setSettings(({ isPlaying: false}))
        audio.current.currentTime = 0;
        setCurrentSong(songs[(currentSong + 1) % songs.length].id)
        setSettings(({ isPlaying: true}))
    }

    const back = () => {
        setSettings(({ isPlaying: false}))
        audio.current.currentTime = 0;
        setCurrentSong(Math.floor(Math.random() * songs.length))
        setSettings(({ isPlaying: true}))
    }

    const repeatMusic = () => {
        audio.current.load()
        play()
        setSettings(({ isPlaying: true}))
    }

    audio.current.addEventListener("ended", () => {
        audio.current.currentTime = 0;
        setTimeout(() => {
            nextSong()
        }, 500)
    });

    return (
        <div className="container">
            <div className="player">
                <Vinyl/>
                <CurrentSongTitle music= {songs[currentSong].title}/>
                <Duration music = {audio.current}/>
                <Controls 
                    next = {nextSong}
                    shuffle={shufflePlayer}
                    back={back}
                    download={download}
                    share={shareOnTwitter}
                    repeat = {repeatMusic}/>
            </div>
            {/* <Social/> */}
        </div>
    )
}
