import React, { useContext } from 'react'
import { AudioSettings } from './Context'
//styles & images
import VinylDisk from './images/disk2.png'
import VinylController from '../images/controller.png'
import './css/vinyl.css'

function Vinyl() {

    const [ settings, setSettings ] = useContext(AudioSettings)

    return (
        <div className="vinyl-container" onClick = {(set) => setSettings((s) => ({ isPlaying: !s.isPlaying}))}>
            <img src={VinylDisk} id="vinyl-disk" className={settings.isPlaying ? "spins":""}/>
            <img src={VinylController} id="vinyl-controller" className={settings.isPlaying ? "starts":""}/>
        </div>
    )
}

export default Vinyl
