import React, { useState, useContext, useCallback, useEffect } from 'react'
import { AudioSettings } from './Context'

//styles
import '../fontawesome/css/all.min.css'
import './css/controls.css'


function Controls({next, back, share, download,repeat }) {

    const [ settings, setSettings ] = useContext(AudioSettings)

    return (
        <div className ="controls">

            <div className ="upper-controls">
                <button onClick={back}>
                    <i className="fas fa-random"></i>
                </button>
                {
                    settings.isPlaying ?
                    (<button className ="btn-control-main" onClick = {(s) => setSettings((s) => ({ isPlaying: !s.isPlaying}))}>
                        <i className ="fas fa-stop"></i>
                    </button>)
                    :
                    (<button className ="btn-control-main" onClick = {(s) => setSettings((s) => ({ isPlaying: !s.isPlaying}))}>
                        <i className ="fas fa-play"></i>
                    </button>)
                }
                <button onClick={next}>
                    <i className="fas fa-forward"></i>
                </button>
            </div>
            <div className="bottom-controls">
                <button onClick={repeat}>
                    <i className="fas fa-recycle"></i>
                </button>
                <button onClick={share}>
                    <i className="fab fa-twitter"></i>
                </button>
                <button onClick={download}>
                    <i className="fas fa-download"></i>
                </button>
            </div>
        </div>
    )
}

export default Controls
