import React, { useState, useEffect, useContext } from 'react'
import { AudioSettings } from './Context'

//styles
import './css/bar.css'

function Duration({music}) {

    const [ settings ] = useContext(AudioSettings)
    const [ duration, setDuration ] = useState(0)
    const [ currentTime , setCurrentTime ] = useState(0)

    const fromat = (num) => {

        num = Math.round(num)
        return Number.isNaN(num) 
        ? '0:00'
        : 
        (
            (num-(num%=60)) /60+(9<num?':':':0')+num
        )
    }

    setInterval(() => {

        const bar = document.getElementById("bar")
        setDuration(fromat(music.duration))
        setCurrentTime(fromat(music.currentTime))

        let px = Math.round(music.duration) / 180
        let width = Math.round(music.currentTime) / px
        bar.style.width = width + "px"
         
        if(width > px){
            clearInterval()
        }
    }, 1000)

    // setTimeout(() => {
    //     document.getElementById("progress").style.visibility ="visible"
    // }, 1500)

    return (
        <div className ="loading">
            <div>
                <h4>{currentTime}</h4>
            </div>
            <div className ="bar-holder" id ="progress">
                <div id ="bar" style = {{ animation: settings.isPlaying ? 'animate .2s infinite alternate' : ''}}></div>
            </div>
            <div>
                <h4>{duration}</h4>
            </div>
        </div>
    )
}

export default Duration
