import React from 'react'

function CurrentSongTitle({music}) {

    const style = {
        textAlign: 'center',
        color: '#f4f5f6',
        fontSize: '16px',
        margin: '0px 10px'
    }
    return (
        <>
        <p 
        style={style}
        className='title-song'
            >{music}</p>
        </>
    )
}

export default CurrentSongTitle
